#root {
  font-family: 'Roboto', sans-serif;
  width: 100%;
}

p, h1 {
  margin: 0;
  padding: 0;
}

main {
  width: 100%;
  max-width: 350px;
  margin: 40px auto;
  padding: 25px;
  border: 1px solid #e5e5e5;
  -webkit-box-shadow: 10px 10px 5px #e5e5e5;
          box-shadow: 10px 10px 5px #e5e5e5;
  border-radius: 5px;
}

.wrapper {
  width: 75%;
  margin: auto;
}

.form {
  margin: 1em auto;
  border: 1px solid #e5e5e5;
  padding: 10px;
  border-radius: 5px;
}

.input {
  display: inline;
  width: 75%;
  border-radius: 5px;
 
}

.input p {
  display: inline-block;
  float: right;
}

.btn {
    color: white;
    padding: 5px 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border-radius: 5px;
    margin-right: 3px;
    cursor: pointer;
    border: none;
    outline: none;
}

.result {
  margin: 2em 0;
}
.result p {
  display: inline;
}

.result span {
  font-size: 0.75em;
}

.right {
  float: right;
}

.inline {
  display: inline;
}

.vat {  
  border-radius: 5px;
  size: 0;
  width: 4em;
  padding: 5px 10px;
  text-align: center;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
   opacity: 1;
}

.title {
  text-align: center;
}


